@import './../App';

.logoTab__container {
  display: inline-flex;
  padding: 114px 0 0 66px;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;

  grid-area: 2 / 2 / 3 / 3;

  max-width: 336px;
  height: 383px;
  border: 1px solid $global-black;
  overflow: hidden;
  position: relative;
  border-radius: 20px;
}


.logoTab__img {
  position: absolute;
  bottom: -10px;
  right: -10px;
}